
import classNames from "classnames";
import { slideshowData } from '../slideshow-R';


const Slide = ({title, body, show=false}) => {
    return(
        <div className={classNames("slide", !show && "hidden")}>
            <h5 id="slideTitle" className={classNames("card-title","pageCount")}><b>{title+1} of {Object.keys(slideshowData).length}</b></h5>
            <p id="slideText" className="card-text">{body}</p>
        </div> 
    ); 
}
export default Slide; 