import React, { useState } from 'react';
import { useHistory } from 'react-router';
import { useApp } from './AppContext';
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles.css";
import Slide from "./Slide"; 
import { slideshowData } from '../slideshow-R';

export function CarouselCards(props) {
    const history = useHistory(); 
    const [curSlide, setCurSlide] = useState(0); 
    const {setCurrPhase, enablePhase} = useApp();
    setCurrPhase("R");
    function isLastSlide() { 
        return curSlide===slideshowData.length-1;
    }
    function prev() { 
        if(curSlide===0) { 
            history.push('/benchmarks'); 
        } else { 
            setCurSlide(curSlide-1);
        }
    }
    function next() { 
        if(isLastSlide()){ 
            history.push('/identifyAndExamine'); 
                    
        } else { 
            setCurSlide(curSlide+1);
            if(curSlide===slideshowData.length-2){
                enablePhase("IE");
            }
        }
    }
    return (
        <>
            <p><b>R</b>eview what was supposed to happen:</p>
            <div id="slideshowCard" className="card">
                <div className="float-container">
                    <div className="card-body, slides, float-child">
                        {slideshowData.map((data, index) => (
                            <Slide key={index} title={index} body={data.body} show={index===curSlide}/>
                        ))}
                    </div>
                </div>
                <div className="arrows">
                    <a id="prev" disabled={curSlide===0} className="prev" onClick={prev} >{"<"}</a> 
                    <a id="next" disabled={curSlide===slideshowData.length-1} className="next" onClick={next} >{">"}</a> 
                </div>
            </div>
        </>
    );
}

export default CarouselCards;
