import React, {useEffect} from "react";
import { tableData } from "../data";
import { useApp } from "./AppContext";



export const Tables = ({phase, nextPhase}) => {
  const {setCurrPhase, enablePhase} = useApp();

    useEffect(
      () => {
          setCurrPhase(phase);
          enablePhase(nextPhase);
      }, [phase, nextPhase]
  ) 
    return (
      <>
        <div className="table-container">
           Step: {tableData.table0[0].step}
        </div>
      </>
    );
  };